.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@font-face {
  font-family: FoobarPro;
  src: url("./font/FoobarPro-Regular.otf") format("opentype");
}
.flatWaveContainer::before{
  background-color: #BBE5ED;
  height:400px;
  width:100vw;
  content:'';
  display: block;
  position: absolute;
  z-index: -1;
}
.waveWrapper{
  width:100%;
  position: absolute;
  height:1100px;
  overflow: hidden;
  top:0px;
  left:0px;
  z-index: 2;
}
.mouseScroll{
  height:50px;
  width:30px;
  border-radius:150px;
  border:1px solid #000;
}
.mouseScroll::before{
  content:'';
  height:10px;
  position: absolute;
  transform: translate(-50%, -50%);
  left:50%;
  top:20%;
  width:3px;
  border-radius: 150px;
  background-color: black;
  opacity: 1;
  animation: mouseScrollerAnimate 1.5s infinite ease-in-out;
}

@keyframes mouseScrollerAnimate {
  0% { top:20%; opacity: 0;}
  10% { top:20%; opacity: 1;}
  100% { top:40%; opacity: 0;}
}

.wave1{
  background-color: white;
  top: -287%;
  left: -55%;
  animation: wave1 90s infinite linear;
}
.wave2{
  background-color: white;
  top: -287%;
  left: -55%;
  opacity: 0.7 !important;
  animation: wave2 90s infinite linear;
}
.wave3{
  background-color: white;
  top: -287%;
  left: -55%;
  opacity: 0.7 !important;
  animation: wave3 90s infinite linear;
}
.wave{
  position: absolute;
  height: 4000px;
  width: 4000px;
  border-radius: 43%;
  box-shadow: 0px 0px 5px #282c34;
  opacity:1;
  content: '';
  /* transform: translate(-50%, -50%); */
}

@keyframes wave1 {
  from { transform: rotate(0deg);}
  to { transform: rotate(360deg);}
}
@keyframes wave2 {
  from { transform: rotate(-35deg);}
  to { transform: rotate(325deg);}
}
@keyframes wave3 {
  from { transform: rotate(-45deg);}
  to { transform: rotate(315deg);}
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
html, body{
  overflow-x: hidden;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
